.header {
  color: $color-default-white;
  background: $gradient-hero-color;
  padding-bottom: 120px;

  @include vp-1365 {
    padding-bottom: 71px;
  }

  @include vp-767 {
    padding-bottom: 9px;
  }

  & .container {
    padding: 0 93px;

    @include vp-1365 {
      padding: 0 35px;
    }

    @include vp-767 {
      padding: 0 5px;
    }
  }
}

.header__block {
  margin: 0 auto;
  max-width: 1190px;
}

.header__main-navigation {
  margin: 14px 0;
  margin-top: 16px;
}

.header__hero {
  margin: 0 auto;
}
